	module.exports = {"data":{"markdownRemark":{"html":"<h2>PromQL</h2>","frontmatter":{"title":"Prometheus","date":"September 03, 2022","path":"/prometheus","tags":["devops","monitoring","alerting","telemetry","prometheus"],"excerpt":""}}},"pathContext":{"prev":{"html":"<h2>Deleting a commit</h2>\n<p>The --soft flag ensures changes from the deleted commit remain staged:</p>\n<pre><code class=\"language-zsh\">git reset --soft HEAD~1\n</code></pre>\n<h2>Rebasing to the tip of local main branch</h2>\n<pre><code class=\"language-zsh\">git rebase main\ngit push --force-with-lease\n</code></pre>\n<p>In case there are conflict execute commands like <code>git add ...</code> and <code>git rebase --continue</code></p>\n<p>When relevant, priority can be given to feature branch changes with:</p>\n<pre><code class=\"language-zsh\">git rebase --main -Xtheirs\n</code></pre>\n<p>followed with:</p>\n<pre><code class=\"language-zsh\">git push --force\n</code></pre>\n<h2>Updating the local branch with respect to remote</h2>\n<pre><code class=\"language-zsh\">git pull --rebase\n</code></pre>\n<h2>Interactive rebase</h2>\n<pre><code class=\"language-zsh\">git log --oneline --decorate --all --graph\n</code></pre>\n<pre><code class=\"language-zsh\">git rebase --interactive HEAD~3\n</code></pre>\n<pre><code class=\"language-zsh\">git push --force origin feat/add-my-feature\n</code></pre>\n<h2>Get back to older commit after several commits pushed to the remote</h2>\n<p>Use <code>git log</code> and <code>git reflog</code> to identify the\ncommit to target and get its sha1, say COMMIT_HASH</p>\n<p>Get back to that commit with:</p>\n<pre><code class=\"language-zsh\">git reset --hard COMMIT_HASH\n</code></pre>\n<p>Finally, push with:</p>\n<pre><code class=\"language-zsh\">git push origin feat/opensearch_operator --force-with-lease\n</code></pre>","id":"/home/nicolas/projects/workshop/src/pages/2022-08-20-git/index.md absPath of file >>> MarkdownRemark","frontmatter":{"date":"2022-08-20T23:16:22.342Z","path":"/git","title":"Git","excerpt":"","tags":["git","devops"]}},"next":{"html":"<p>A list of all tasks can be displayed with:</p>\n<pre><code class=\"language-zsh\">gradle tasks\n</code></pre>\n<h3>Gradle init task:</h3>\n<p>This task initiates a project including a gradlew wrapper script:</p>\n<pre><code class=\"language-zsh\">mkdir demo\ncd demo\ngradle init\ntree .\n</code></pre>\n<p>Application can be run with:</p>\n<pre><code class=\"language-zsh\">./gradlew run\n</code></pre>\n<p>and bundled with:</p>\n<pre><code class=\"language-zsh\">./gradlew build\n</code></pre>\n<p>This produces two archives called <code>app.tar</code> and <code>app.zip</code> in `<code></code>app/build/distribution<code></code></p>\n<h3>Gradle jar task: <a href=\"https://docs.gradle.org/current/samples/sample_building_kotlin_libraries.html#customize_the_library_jar\">customizing the library jar</a></h3>\n<p>Properties to set if required in the build.gradle.kts file: version, name and group i.e. maven coordinates:</p>\n<pre><code class=\"language-kts\">version=\nname=\ngroup=\n</code></pre>\n<p>Run the jar task:</p>\n<pre><code class=\"language-zsh\">./gradlew jar\n</code></pre>\n<p>Verifying that an archive is valid:</p>\n<pre><code class=\"language-zsh\">jar tf lib/build/libs/lib.jar\n</code></pre>\n<p>Verifying that archive is signed:</p>\n<pre><code class=\"language-zsh\">jarsigner -verify lib/build/libs/lib.jar\n</code></pre>\n<h3>Displaying the tasks sequence</h3>\n<pre><code class=\"language-zsh\">./gradlew test --console=plain\n</code></pre>\n<h3>The dependencies task</h3>\n<pre><code class=\"language-zsh\">./gradlew :app:dependencies --configuration runtimeClasspath\n</code></pre>\n<p>This shows dependencies of a given classpath, e. g. runtime.</p>\n<h3>Generating sources JAR:</h3>\n<pre><code class=\"language-kts\">java {\n    withSourcesJar()\n}\n</code></pre>\n<h3>Publishing</h3>\n<ul>\n<li><a href=\"https://docs.gradle.org/current/userguide/publishing_setup.html#sec:basic_publishing\">Basic Publishing</a></li>\n<li><a href=\"https://docs.gradle.org/current/userguide/publishing_signing.html#publishing_maven:signing\">Signing Artifacts</a></li>\n</ul>","id":"/home/nicolas/projects/workshop/src/pages/2022-09-09-gradle/index.md absPath of file >>> MarkdownRemark","frontmatter":{"date":"2022-09-09T14:02:00.438Z","path":"/gradle","title":"Gradle","excerpt":"","tags":["gradle","devops","continuous integration","build","java","kotlin"]}}}}

